<!--  -->
<template>
  <div>
  
        <Zi goback="true" data="ele.me">
          <h1 slot="left">ele.me</h1> 
        </Zi>
       
        <div class="header">
          <div class="top">
            <span class="zuo">当前定位城市</span>
            <span class="you">定位不准确，请在城市列表中选择</span>
          </div>
          <div class="top2">
             <span class="zuo">{{str}}</span>
            <span class="you">></span>
          </div>
          <div class="main">
            <p style="border-bottom:1px solid #ccc;padding-left:-0.5rem;line-height:1rem">热门城市</p>
            <ul>
              <router-link :to="{name:'Detail',params:{id:item.id}}" tag="li" v-for="(item,index) of arr" :key="index">
                {{item.name}}
              </router-link>
            </ul>
          </div>
        </div>
        <div class="main"  v-for="(item,index) of list2" :key="index">
          <p v-if="item=='A'">
             <span>{{item}}</span>
               <span class="zi">(按字母表排序)</span>
            </p> 
             <p v-else>
             <span>{{item}}</span>
            </p> 
            <div class="all">
               <router-link tag="li" :to="{name:'Detail',params:{id:i.id}}"   v-for="(i,inde) in list[item]" :key="inde">{{i.name}}</router-link>
            </div>
               
        </div>
  </div>
</template>

<script>
import Zi from "../../components/header"
export default {
  data () {
    return {
        arr:[],
        str:"",
        list:{},
        list2:[],
        img:`
        <span>登陆</span>
        <span>注册</span>
        `,
        items: [],
        a:0
    };
  },

  components: {
    Zi
  },

  computed: {},

  mounted(){
   
      var that=this
     this.http.get("http://elm.cangdu.org/v1/cities?type=guess").then((d)=>{
       console.log()
       that.str=d.data.name
     })

      this.http.get("http://elm.cangdu.org/v1/cities?type=hot").then((d)=>{
          console.log(d)
          that.arr=d.data

      })
      this.http.get("http://elm.cangdu.org/v1/cities?type=group").then((d)=>{
        console.log(d)
        that.list=d.data
        console.log(that.list.A)
       that.list2=Object.keys(d.data).sort()
      })
  },

  created(){},

  methods: {

}
}
</script>
<style scoped lang="scss">
.header{
  display: flex;
  flex-direction: column
}
ul,.all{
  display: flex;
  flex-wrap: wrap;
  li{
    width: 24%;
    text-align: center;
    overflow: hidden;
    list-style: none;
   
    
  }
}
.header{
  z-index: -2;
  .top,.top2{
     line-height: 1rem;padding-left:0.2rem;color: #3190e8;
   
    height: 1rem;
    display: flex;border-bottom: 1px solid #ccc;
     span{display: block;line-height: 1rem;}
     .zuo{width:30%}
     .you{width:70%;color: #d4d4d4;font-weight: bold}

  }
  
  .top2{
     border-bottom: 0.1rem solid #ccc;
    .zuo{width:90%}
     .you{width:10%;color: #d4d4d4;font-weight: bold}
  }
}
.main{
  p{padding-left:0.5rem}
  p,li{line-height: 1rem;height: 1rem;}
  li{border: 1px solid #d4d4d4;}
}
.zi{color: #ccc;font-size: 0.2rem;margin-left: 0.2rem}
</style>


